/**
 * The geosuggest module
 */
 .geosuggest {
  font-size: 18px;
  font-size: 1rem;
  position: relative;
  width: 50%;
  margin: 1em auto;
  text-align: left;
}
.geosuggest__input {
  width: 100%;
  border: 2px solid transparent;
  -webkit-box-shadow: 0 0 1px #3d464d;
          box-shadow: 0 0 1px #3d464d;
  padding: .5em 1em;
  -webkit-transition: border 0.2s, box-shadow 0.2s;
          -webkit-transition: border 0.2s, -webkit-box-shadow 0.2s;
          transition: border 0.2s, -webkit-box-shadow 0.2s;
          -o-transition: border 0.2s, box-shadow 0.2s;
          transition: border 0.2s, box-shadow 0.2s;
          transition: border 0.2s, box-shadow 0.2s, -webkit-box-shadow 0.2s;
}
/* .geosuggest__input:focus {
  border-color: #267dc0;
  box-shadow: 0 0 0 transparent;
} */
.geosuggest__suggests {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  max-height: 25em;
  padding: 0;
  margin-top: -1px;
  background: #fff;
  border: 2px solid #267dc0;
  border-top-width: 0;
  overflow-x: hidden;
  overflow-y: auto;
  list-style: none;
  z-index: 5;
  -webkit-transition: max-height 0.2s, border 0.2s;
          -o-transition: max-height 0.2s, border 0.2s;
          transition: max-height 0.2s, border 0.2s;
}
.geosuggest__suggests--hidden {
  max-height: 0;
  overflow: hidden;
  border-width: 0;
}

/**
 * A geosuggest item
 */
.geosuggest-item {
  font-size: 18px;
  font-size: 1rem;
  padding: .5em .65em;
  cursor: pointer;
}
.geosuggest-item:hover,
.geosuggest-item:focus {
  background: #f5f5f5;
}
.geosuggest-item--active {
  background: #267dc0;
  color: #fff;
}
.geosuggest-item--active:hover,
.geosuggest-item--active:focus {
  background: #ccc;
}

/* !!! */
.geosuggest {
  width: 100%;
}

.geosuggest__input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
}

.geosuggest__suggests {
  border: none;
  -webkit-box-shadow: 0 0 1px #3d464d;
          box-shadow: 0 0 1px #3d464d;
}

.geosuggest__item {
  padding: .2em 1em;
}

.geosuggest__item--active {
  background: #267dc0;
  color: #fff;
}


